
import { Component, OnInit ,Input } from '@angular/core';
import { BaseChartComponent} from '../base-chart/base-chart.component';
import {  Station} from '../../Entities/Station';

import {  MotorService} from '../../Services/MotorService';
 
@Component({
  selector: 'motor-status-chart',
  templateUrl: './motor-status-chart.component.html' 
     
})
export class MotorStatusChartComponent  extends BaseChartComponent {

   stationlist : Station[] ;
   station:Station;
  private options;
  private data;
  private chart;  

  v:any;
  constructor(private ss :MotorService ){ 
    
    super();        
    this.getStationStatus();
   
  }

  getStationStatus(){
    this.ss.getOverAllMotorStatus(2,-1).subscribe(result =>{ this.v=result; this.drawGraph() }) ;     
     
  }

   
  drawGraph(){

     
    console.log("DrawGraph Evolution...");  

    /* var v = [
      ['StationHealth', 'NumOfStation'],
      ['Green',     500],
      ['Amber',     200],
      ['Red',  20]
    ]
    */
   
    this.data = this.createDataTable(this.v );

    this.options = {
      /*title: 'Evolution, 2014', */
      chartArea:{left:0,top:0,width:"1000",height:"500"} ,
      hAxis: {
        title: 'Value in USD',
        minValue: 0
      } ,
      'legend':'none',
      vAxis: {
        title: 'Members'
      },
      "colors":["#008000","#FFC200", "#FF0000"],
      'width':300,
      'height':200, 
        pieHole: 0.4   
     

    };

    this.chart = this.createBarChart(document.getElementById('donutChart'));
    this.chart.draw(this.data, this.options);
  }
}


