import { Component, OnInit } from '@angular/core';
import {MotorService} from '../../services/motor.service';
import {Motor} from '../../Entities/Motor';

@Component({
  selector: 'app-motor-list',
  templateUrl: './motor-list.component.html',
  styleUrls: ['./motor-list.component.css']
})
export class MotorListComponent implements OnInit {
  public motorList;
  public JSON;
  public locationImage;
  constructor(private motorService:MotorService) {
    this.JSON=JSON;
    this.locationImage="/assets/images/location.png";;
   }

  ngOnInit() {
    this.getMotorList();
  }
  getMotorList(){
    this.motorService.getMotorList().subscribe(res=>{
      this.motorList=res.value;
      console.log("Received by observer"+JSON.parse(this.motorList[0].MotorObject).Name);
    });
  }

}



