import  { Injectable }  from '@angular/core';
import { Headers, Http } from '@angular/http';
import 'rxjs/add/operator/toPromise';

import { Plant} from  '../Entities/Plant';


//the injectable attribute to generate the metadata to make the service available through 
//Dependency Injustion(DI) in other modules.
@Injectable()
export class PlantService
{
	
	 constructor(private http: Http) { }
	 
   private PlantUrl='/plantlist.json'
   private PlantUrl2='https://hclmotorstorageaccount.table.core.windows.net/Stations(PartitionKey="pk1",RowKey="rk1")?$select=StationObeject&st=2017-08-30T08%3A15%3A00Z&se=2017-10-21T08%3A15%3A00Z&sp=raud&sv=2016-05-31&tn=stations&sig=4GLyLYg3YBSZqR5TydJ5nK7QqzuZRc6x9%2B52RFih%2B9M%3D';

   plantlist : Plant[];
   plant :Plant;
 
	GetPlantAsync() : Promise<Plant[]> {
		 return this.http.get(this.PlantUrl)		     
             .toPromise()
			  .then(response => response.json() as Plant[] ) 
             .catch(this.handleError);
	}

	 

	GetPlantById(id:number) :Promise<Plant>  {
		   
		return this.http.get(this.PlantUrl)		     
		.toPromise()
		 .then(response => this.extractData(response,id) )	
		//.then(response => response.json() as Hero[]
		.catch(this.handleError);
         
	}
	
	private extractData(res,id)
	{
		let data =res.json().find(x=>x.Id==id);
	   
		return  data as Plant;
	}

	private handleError(error: any): Promise<any> {
  console.error('An error occurred', error); // for demo purposes only
  return Promise.reject(error.message || error);
}
}


