import { Component, OnInit } from '@angular/core';
import { BaseChartComponent} from '../base-chart/base-chart.component';

import {  StationService} from '../../Services/StationService';
import {config} from  '../../Configuration/MasterConfiguration';

@Component({
  selector: '<pi-chart>',
  templateUrl: './pi-chart.component.html' 
   
})
export class PiChartComponent  extends BaseChartComponent {
  private options;
  private data;
  private chart;
 
  v:any;
  charttype :string
  constructor (private ss :StationService)
  { super();
    this.getStationStatus();
     }

    getStationStatus(){
    this.ss.getStationStatus(config.PlantName).subscribe(result =>{ this.v=result;
      this.drawGraph() }) ;     
     
  }

  drawGraph(){
    console.log("DrawGraph ...");  
    
    /*var v = [
      ['StationHealth', 'NumOfStation'],
      ['Green',     500],
      ['Amber',     200],
      ['Red',  20]
    ]
   */
    this.data = this.createDataTable(this.v );

    this.options = {
      /*title: 'Evolution, 2014', */
      chartArea:{left:0,top:0,width:"1000",height:"500"} ,
      hAxis: {
        title: 'Value in USD',
        minValue: 0
      },
      is3D: true,
      'legend':'none',
      vAxis: {
        title: 'Members'
      },
      "colors":["#008000","#FFC200", "#FF0000"],
      'width':300,
      'height':200, 
     

    };

    this.chart = this.createBarChart(document.getElementById('piechart'));
    this.chart.draw(this.data, this.options);
  }
}


