import { Component, OnInit, OnDestroy } from '@angular/core';
import { GraphService }       from '../../services/graph.service'; 

@Component({

  selector: 'app-graph',

  templateUrl: 'motor.component.html',

  styles: [`

    .chat {

      margin-top: 100px;

      margin-left: auto;

      margin-right: auto;

      width: 10%;

      font-family: 'Georgia';

      font-size: 200%;

    }

  `],

  providers: [GraphService]

})

export class MotorComponent implements OnInit, OnDestroy {

  messages = [];

  connection;

  message;

 

  constructor(private graphService: GraphService) { }

 

  sendMessage() {
    console.log('Message : '+this.message);
    this.graphService.sendMessage(this.message);
    
    this.message = '';

  }

 

  ngOnInit() {

    this.connection = this.graphService.getMessages().subscribe(message => {
      console.log('Message received from service...'+message);
      this.messages.push(message);

    })

  }

 

  ngOnDestroy() {

    this.connection.unsubscribe();

  }

}



