import { Component, OnInit } from '@angular/core';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import * as io from 'socket.io-client';

@Component({
  selector: 'app-test-ws',
  templateUrl: './test-ws.component.html',
  styleUrls: ['./test-ws.component.css']
})
export class TestWsComponent implements OnInit {
  //private url ='wss://hclmotorwebapplication.azurewebsites.net';
  private url = 'wss://' + location.host;
  private socket;
  constructor() { 
    console.log("Log for Wss: "+this.url);
    this.socket = io(this.url);
    console.log("connected...");
  }

  ngOnInit() {
  }

}



