import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { CollapseModule } from 'ngx-bootstrap';
import { AppComponent } from './app.component';
import { NavbarComponent } from './navbar/navbar.component';
import { TestComponent } from './test/test.component';
import { GoogleChartComponent } from './google-chart/google-chart.component';
import { EvolutionComponent } from './evolution/evolution.component';
import {RouterModule,Routes} from '@angular/router';
import { HistoricalDataComponent } from './historical-data/historical-data.component';
import { SearchComponentComponent } from './Components/search-component/search-component.component';
import {HttpModule} from '@angular/http';
import { MotorManagementComponent } from './motor-management/motor-management.component';
import {FormsModule} from '@angular/forms';
import {GitHubService} from './services/git-hub.service';
import { AddMotorFormComponent } from './forms/add-motor-form/add-motor-form.component';
import { ReactiveFormsModule }          from '@angular/forms';
import { MotorListComponent } from './Components/motor-list/motor-list.component';
import { hclplantdashboard } from './Components/Plant/hclplantdashboard';
 import { PlantComponent } from './Components/Plant/PlantComponent';
 import { StationComponent } from './Components/Station/StationComponent';
 import {  PlantService} from './Services/PlantService';
 import {  StationService} from './Services/StationService';
 import {  MotorService} from './Services/MotorService';
 import {  NotificationService} from './Services/NotificationService';
 import { HashLocationStrategy, LocationStrategy } from '@angular/common'; 
import { BaseChartComponent } from './Components/base-chart/base-chart.component';
import { PiChartComponent } from './Components/pi-chart/pi-chart.component';
import { MotorStatusChartComponent } from './Components/motor-status-chart/motor-status-chart.component';
import { NotificationsComponent } from './Components/notifications/notifications.component';
import {config} from  './Configuration/MasterConfiguration';
import { MotorComponent } from './Components/motor/motor.component';
import { ScreenTitleComponent } from './Components/screen-title/screen-title.component';
import { StationlistComponent } from './Components/stationlist/stationlist.component';
import { TestWsComponent } from './Components/test-ws/test-ws.component';




const appRoutes:Routes=[{
  path:'graph',
  component:EvolutionComponent
  },{
    path:'motor',
    component:TestWsComponent
  },{
    path:'MotorManagement',
    component:MotorManagementComponent
  },
  {
     path: 'plant',
      component: hclplantdashboard,
      data :{ }
     },
  
  { path: '',
    redirectTo: '/historical',
    pathMatch: 'full'
  },
  { path: 'station/:id',      component: StationComponent }
];


@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    TestComponent,
    GoogleChartComponent,
    EvolutionComponent,
    HistoricalDataComponent,
    SearchComponentComponent,
    MotorManagementComponent,
    AddMotorFormComponent,
    MotorListComponent,
    hclplantdashboard,
  PlantComponent,
  StationComponent, 
  BaseChartComponent,
  PiChartComponent,
  MotorStatusChartComponent,
  NotificationsComponent,
  StationlistComponent,
  MotorComponent,
  ScreenTitleComponent,
  TestWsComponent
    ],
  imports: [
    BrowserModule,
    CollapseModule,
    HttpModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule.forRoot(appRoutes),
  ],
  providers: [PlantService,StationService,NotificationService,MotorService,config,{provide: LocationStrategy, useClass: HashLocationStrategy}],
  bootstrap: [AppComponent]
})
export class AppModule { }



