import { Component, OnInit } from '@angular/core';
import { NgForm} from '@angular/forms';
import{Motor} from '../../Entities/motor';
import {GitHubService} from '../../services/git-hub.service';



declare var test: any;
declare var AzureStorage:any;
var tableService : any;


@Component({
  selector: 'motor-form',
  templateUrl: './add-motor-form.component.html',
  styleUrls: ['./add-motor-form.component.css']
})
export class AddMotorFormComponent implements OnInit {
model :Motor;
rowKey:string;
submitted:boolean;
public StationList;
public JSON;
public fullImagePath1="/assets/images/1.jpg";
public fullImagePath2="/assets/images/3.jpg";
public fullImagePath3="/assets/images/4.jpg";
public fullImagePath4="/assets/images/5.jpg";

constructor(private githubService:GitHubService) { this.JSON = JSON;}
ngOnInit() {
        this.model = new Motor("","","","","","HCLPlant","","")
console.log(this.model);
  this.submitted = false;
    this.GetUserList();
  }
  GetUserList(){
    this.githubService.getStationList().subscribe(
      res=>{
        console.log(res.value);
        this.StationList=res.value;
      }
    
    );
  }

  onSubmit(addMotorForm: NgForm) { 
    var tableUri = 'https://hclpocstorageaccount.table.core.windows.net:443/Motor';
    tableService=new AzureStorage.createTableServiceWithSas(tableUri, '?st=2017-09-07T07%3A29%3A00Z&se=2018-08-31T07%3A29%3A00Z&sp=raud&sv=2016-05-31&tn=motor&sig=EH4rL1HVvVTTSuVuzJMQMpfSWfDRPP8x8X85hSgn5CY%3D');
    this.rowKey=Date.now()+"";    
var entity = {
     PartitionKey: {'_': 'partitionKey'},
     RowKey: {'_': this.rowKey},
     StationName: {'_': this.model.StationName},
     PlantName: {'_': this.model.PlantName},
     Status: {'_': "Green"},
     MotorObject:{'_':JSON.stringify(this.model)}
  //dateValue: entGen.DateTime(new Date(Date.UTC(2011, 10, 25))),
  //complexDateValue: entGen.DateTime(new Date(Date.UTC(2013, 02, 16, 01, 46, 20)))
};
console.log ("Data to be inserted"+JSON.stringify(this.model));

tableService.insertEntity('Motor', entity, function(error, result, response) {
  if (!error) {
    console.log('Success');
  }
else
  console.log(error);
});
    this.submitted = true; 
  }

  // TODO: Remove this when we're done
  get diagnostic() { return JSON.stringify(this.model); }
}



