import { Component, OnInit } from '@angular/core';
import {GitHubService} from '../../services/git-hub.service';


@Component({
  selector: 'app-search-component',
  templateUrl: './search-component.component.html',
  styleUrls: ['./search-component.component.css']
})
export class SearchComponentComponent implements OnInit {
public StationList;
public JSON;
  constructor(private githubService:GitHubService) { this.JSON = JSON;}

  ngOnInit() {
    
  }
  GetUserList(){
    this.githubService.getStationList().subscribe(
      res=>{
        console.log(res.value);
        this.StationList=res.value;

        console.log("Test1 :: "+ this.StationList[0].StationObeject);


        console.log("Test :: "+ JSON.parse(res.value[0].StationObeject).StationName);
      }
    
    );
  }


}



