import { Component, OnInit, Input } from '@angular/core';
import {  StationService} from '../../Services/StationService';

import {config} from  '../../Configuration/MasterConfiguration';

@Component({
  selector: 'stations',
  templateUrl: './stationlist.component.html' 
})
export class StationlistComponent implements OnInit {

  constructor (private ss :StationService)
  {       
     this.JSON=JSON;
  }
      
  @Input() currentPlantId : number;    
  stationlist : any;
  JSON :any;
       
  ngOnInit(): void {      
    
     if(this.currentPlantId== null)
     { 
          this.currentPlantId=2;   
     }      
     this.GetStationListForPlant();           
  }
   
 GetStationListForPlant() :void
  {                
  this.ss.GetStationListforPlant(config.PlantName).subscribe(data => this.stationlist = data); 
  }
}



